html {
  height: 100%;
}
body {
  background: -webkit-radial-gradient(circle farthest-corner at right bottom,#564AD9 0,#BCCAFB 100%);
  background: -o-radial-gradient(circle farthest-corner at right bottom,#564AD9 0,#BCCAFB 100%);
  background: radial-gradient(circle farthest-corner at right bottom,#564AD9 0,#BCCAFB 100%);
  font-family: 'Montserrat', sans-serif;
  color: #FFF;
  min-height: 100%;
}
h1{
  font-family: 'Oswald', sans-serif;
}
h2 {
  font-size: 36px;
  line-height: 40px;
}
h3 {
  font-weight: 300;
}
.App {
  margin: 0 auto;
  width: 980px;
  padding: 160px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.Preview {
  -ms-flex: 0 1;
      flex: 0 1;
  -ms-flex-positive: 0;
      flex-grow: 0;
}
code {
  display: block;
  background: rgba(255, 255, 255, .3);
  padding: 20px;
  color: rgba(24, 24, 24, 0.9);
  font-weight: 500;
  font-family: 'Montserrat', sans-serif;
  margin-bottom: 20px;
}

.PreviewBox {
  background: #FFF;
  padding: 10px;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.07), 0 2px 5px rgba(0, 0, 0, 0.03);
          box-shadow: 0 0 1px rgba(0, 0, 0, 0.07), 0 2px 5px rgba(0, 0, 0, 0.03);
}

.Description {
  padding: 0 40px;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-positive: 1;
      flex-grow: 1;
}


@media (max-width: 940px) {

  .App {
    display: block;
    width: 100%;
  }

  .Preview {
    -ms-flex-item-align: center;
        align-self: center;
    margin: 0 auto;
    width: 380px;
  }
}



